.call-action {

    padding: rem-calc(100 0);

    &-content {
        background-color: $colorCta;
        border-radius: rem-calc(20);
        padding: rem-calc(50px 0);
    }

    &-box {
        max-width: rem-calc(840);
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;

        &-text {
            max-width: rem-calc(500);
            color: $colorLight;
        }
    }

    &-title {
        @include text(20, 600, 1);
        margin-bottom: rem-calc(10);
    }
    
    &-text {
        @include text(16, 400, 1.3);
    }
}